<template>
  <div class="body-bg">
    <div class="login-to">
      <div class="login-logo">
        <b class="logo"></b>
      </div>
      <div class="login-info">
        <div class="head">用户登录</div>
        <input name="telephone" v-validate="{ rules: { regex: /^(13[0-9]|145|147|15[0-235-9]|170|176|177|18[0-9])[0-9]{8}$/} }" :class="{'is-danger': errors.has('telephone') }" placeholder="手机号码" class="input-style telephone" v-model="userName">
        <input name="password" v-validate="{ rules: { regex: /.{6,20}$/} }" :class="{'is-danger': errors.has('password') }" placeholder="密码" type="password" class="input-style passwd-style" v-model="passwd">
        <p class="forgotpsd">
          <router-link to="forgot">
            <span>
              忘记密码
            </span>
          </router-link>
        </p>
        <div class="v-code">
          <input  name="vcode" v-validate="{ rules: { regex: /^.{4}$/} }" :class="{'is-danger': errors.has('vcode') }"  placeholder="校验码" class="input-style code" @keyup.enter="login" v-model="vCode">
          <img class="vcode" @click="refreshVcodeUrl" :src="vCodeUrl" alt="">
        </div>
        <div class="rempsd">
          <!--<el-checkbox>记住密码</el-checkbox>-->
        </div>
        <div class="error" v-show="error"><i class="icon-font icon-error">&#xe601;</i> {{errorMsg}}</div>
        <template v-if="fields.fields&&fields.fields.password">
          <div :class="{'is-ok': !errors.any()&&userName&&passwd&&vCode}" class="jump-to" @click="login">登录</div>
        </template>
        <p class="register">
          没有账户?<router-link to="register"><span>立即注册>></span></router-link>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import LoginAPI from '@/api/loginAPI'
import '@/assets/js/security'

export default {
  name: 'login',
  data () {
    return {
      /**
       * rsa publickey 初始化参数
       * @type {String}
       */
      rediskey: '',
      exponent: '',
      modulus: '',
      vCodeUrl: process.env.DOMAIN + '/bshop/login/captcha/get',
      /**
       * 用户名 密码 验证码
       * @type {String}
       */
      userName: '',
      passwd: '',
      vCode: '',
      errorMsg: '',
      error: false
    }
  },
  components: {
  },
  mounted: function () {
    /**
     * 初始化公钥
     * @param  {[type]} res [description]
     * @return {[type]}     [description]
     */
    LoginAPI.getRSAPublicKey().then(res => {
      if (res.data.code === 0) {
        let callbackData = res.data.data
        this.rediskey = callbackData.rediskey
        this.exponent = callbackData.exponent
        this.modulus = callbackData.modulus
      } else {
        console.log('初始化公钥失败')
      }
    })
  },
  methods: {
    /**
     * 刷新验证码
     * @return {[type]} [description]
     */
    refreshVcodeUrl () {
      this.vCodeUrl = process.env.DOMAIN + '/bshop/login/captcha/get?v=' + new Date().getTime()
    },
    /**
     * 加密验证码
     * @param  {[type]} text [description]
     * @return {[type]}      [description]
     */
    encryptRSA (text) {
      var key = window.RSAUtils.getKeyPair(this.exponent, '', this.modulus)
      return window.RSAUtils.encryptedString(key, text)
    },
    /**
     * 登录按钮
     * @return {[type]} [description]
     */
    login () {
      if (!(!this.errors.any() && this.userName && this.passwd && this.vCode)) {
        console.log('前端输入验证不通过，点击按钮无效！')
        return
      }
      let postObj = {
        'loginAccount': this.userName,
        'password': this.encryptRSA(this.passwd),
        'captcha': this.vCode,
        'rediskey': this.rediskey
      }
      LoginAPI.login(postObj).then(res => {
        if (res.data.code === 0) {
          console.log('登录成功')
          console.log(res.data.data)
          let shopStatus = res.data.data.shopStatus
          console.log(shopStatus)
          let user = res.data.data
          window.localStorage.shopId = user.shopId
          window.localStorage.userId = user.userId
          window.localStorage.avatr = user.avatar
          window.localStorage.serverLevel = user.serverLevel
          window.localStorage.user = JSON.stringify(user)
          this.$store.commit('SET_USER_INFO', user)
          switch (shopStatus) {
            case 0:
              this.$router.push('/CreateShop')
              break
            case 1:
              this.$router.push('/certify')
              break
            case 2:
              this.$router.push('/certify')
              break
            case 3:
              this.$router.push('/certify')
              break
            case 4:
              this.$router.push('/index')
              break
            case 5:
              this.$router.push('/certify')
              break
            case 6:
              this.$router.push('/index')
              break
            case 7:
              this.$message.error('运营到期已关闭')
              break
            default:
              this.$router.push('/index')
              break
          }
//          this.$router.push({ path: '/index' })
        } else {
          this.refreshVcodeUrl()
          this.errorMsg = res.data.message
          this.error = true
        }
      })
    }
  }
}
</script>

<style lang="less" rel="stylesheet/less">
  html,body,#app{
    height: 100%;
  }
  .body-bg{
    font-family: MicrosoftYaHei;
    width:100%;
    height: 100%;
    /*height: 960px;*/
    overflow-y: auto;
    /*overflow: hidden;*/
    -moz-background-size: cover;
    -webkit-background-size:cover;
    .login-to{
      margin-top: 65px;
      width: 100%;
      /*height: 100%;*/
      .login-logo{
        text-align: center;
        margin-bottom: 40px;
        .logo{
          display: inline-block;
          width: 396px;
          height: 93px;
          background: url("../../assets/img/login/logo-small.png") no-repeat;
        }
      }
      .login-info{
        width:398px;
        height: 510px;
        background: #FFFFFF;
        border: 1px solid #E8E8E8;
        border-radius: 4px;
        margin: 0 auto;
        padding: 48px 40px 0 40px;
        .head{
          text-align: center;
          font-size: 18px;
          color: #33475B;
          letter-spacing: 0;
          margin-bottom: 40px;
        }
        .input-style {
          width: 400px;
          height: 56px;
          background: #F5F8FA;
          border-radius: 4px;
          margin: 0 auto;
          font-size: 14px;
          color: #7C98B6;
          border: none;
          padding-left:16px;
          border:1px solid #F5F8FA;
          box-sizing: border-box;
        }
        .input-style:focus{
          border:1px solid #02AAAA;
          outline: none;
        }
        .passwd-style {
          margin-top:16px;
        }
        .is-danger{
          border:1px solid #FF3B6E
        }
        .v-code{
          position: relative;
          .code{
            .el-input__inner{
              margin-bottom: 8px;
            }
          }
          .vcode{
            position: absolute;
            top: 1px;
            height: 54px;
            width: 120px;
            right: 0px;
          }
        }
        .forgotpsd{
          width: 400px;
          font-size: 14px;
          color: #33475B;
          text-align: right;
          padding: 4px 0 8px 0;
          margin: 0 auto;
          text-decoration: underline;
          a{
            color: dodgerblue;
            span{
              color: dodgerblue;
            }

          }
        }
        .rempsd{
          width: 400px;
          margin: 16px auto;
          .el-checkbox__inner{
            border: 2px solid #CBD6E3;
            border-radius: 2px;
          }
        }
        .error{
          width:366px;
          height: 24px;
          background: #FF3B6E;
          background: rgba(255,255,255,0.90);
          border: 1px solid #FF3B6E;
          /*border: 1px solid rgba(255,255,255,0.80);*/
          border-radius: 4px;
          margin-top: 40px;
          font-size: 14px;
          color: #FF3B6E;
          padding: 15px 16px;
          line-height: 24px;
          .icon-error{
            color: #FF3B6E;
          }
        }
        .jump-to{
          width:400px;
          height: 56px;
          line-height: 56px;
          margin: 45px auto 8px;
          border: 1px solid #DFE3EB;
          border-radius: 4px;
          font-size: 18px;
          text-align: center;
          cursor: pointer;
          color: #B0C1D4;
          background: #EAF0F6;
        }
        .jump-to.is-ok{
          background: #5488F9;
          color: #fff;
        }
        .register{
          width:270px;
          padding-left: 130px;
          font-size: 14px;
          color: #666666;
          span:last-child{
            color: dodgerblue;
          }
        }
      }
  }

    /*.login-wraper {*/
      /*width:350px;*/
      /*margin:200px auto;*/
      /*.vcode{*/
        /*cursor: pointer;*/
      /*}*/
      /*h3{*/
        /*text-align: center;*/
      /*}*/
    /*}*/
  }
</style>



